body {
  margin: 0;
  padding: 0;
  color: #2f2f2f ;
  font-family: "Roboto", "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  background-color: #d1d1d1;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  padding: 50px;
  padding-top: 20px
}

.list {
  background-color: #ffffff;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0 4px 6px 0 rgba(0,0,0,0.2);
}

.element {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  overflow: hidden;
  font-size: 17px;
  font-weight: 400;
  color: #414141;
  margin: 4px;
  border-radius: 5px;
}

.element-disabled {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: rgb(217, 221, 230);
  color: #686868;
  box-sizing: border-box;
  padding: 5px;
}

.element-main {
  cursor: ns-resize;
  background-color: #87abee;
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  padding: 5px
}

.element-select {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #b9cbecb7;
  padding: 5px
}

.element-main:hover {
  box-shadow: 0 6px 8px 0 rgba(0,0,0,0.2);
}

.loader {
  border: 14px solid #f3f3f3; /* Light grey */
  border-top: 14px solid #87abee; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

